:root {
    --primary: #38c3ff;
    --primarylight: #45ffe1;
    --white: #d4d4d4;
    --white2: #ffffff;
    --secondary: #ef6bff;
    --pink: #ef6bff;
    --orange: #ffab6f;
    --red: #ff6868;
    --green: #45ffe1;
    --yellow: #fff78d;
    --purple: #a552ff;
    --blue: #38c3ff;
    --black: #2b2b2b;
    --dark: #161616;
    --nav: #222222;
}

* {
    box-sizing: border-box;
}

p {
    margin: 0;
    font-family: 'Lato', sans-serif;
    color: #d4d4d4;
    color: var(--white);
    
}

bold {
    color: #45ffe1;
    color: var(--green);
}

html {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
}

body {
    background-color: #161616;
    background-color: var(--dark);
    margin: 0;
    padding: 0;
}

a {
    color: #38c3ff;
    color: var(--primary);
    text-decoration: none;
    text-transform: lowercase;
    font-family: 'Inter', sans-serif;
    font-weight: normal;
}

a:hover {
    color: #ef6bff;
    color: var(--secondary);
}

h1 {
    font-family: 'Montserrat', sans-serif;
    color: #d4d4d4;
    color: var(--white);
    font-size: 30px;
    margin-top: 0;
    margin-bottom: 0;
}

@media (max-width: 400px) {
    h1 {
        font-size: 26px;
    }
}

h5 {
    line-height: 0;
    font-weight: lighter;
    font-family: 'Source Sans Pro', sans-serif;
    text-transform: uppercase;
    font-size: 22px;
    color: #38c3ff;
    color: var(--primary);
    margin-top: 20px;
}


.portfolio-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.main-content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 6 1;
            flex: 6 1;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}


.projects-content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.projects-display {
    margin-top: 0;
}

.complete-project {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    margin-right: 30px;
    margin-left: 30px;
}

.project-card {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    margin-bottom: 20px;
    max-width: 330px;
    min-width: 330px;
    min-height: 500px;
}

.project-card a {
    color: var(--white);
    padding: 1px;
    font-size: 20px;
    font-weight: bold;
}

.project-card a:hover {
    color: var(--secondary);
    transition: 0.2s;
}

.project-name {
    text-align: left;
}

.project-links {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    text-align: left;
    margin-top: 10px;
    margin-right: 30px;
}

.project-img img {
    width: 100%;
    height: auto;
    box-shadow: 2px 3px 5px rgba(0,0,0,0.5);
    border-radius: 4px;
    margin-bottom: 1.5rem;
}

.project-description {
    max-height: 100px;
    min-height: 100px;
    margin-top: 5px;
}

.project-card p {
    font-family: "Lato", sans-serif;
    font-weight: lighter;
    font-size: 16px;
    line-height: 22px;
}
.about-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    height: 100%;
    background-color: var(--white);
    padding: 70px 0;
}

.about-div {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    max-width: 830px;
}

.about-div p {
    font-family: "Lato", sans-serif;
    font-weight: lighter;
    font-size: 18px;
    color: var(--white);
    line-height: 30px;
}

.about-box {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: center;
            justify-content: center;
    max-width: 820px;
    margin-top: 10px;
}

.about-left {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 1 1;
            flex: 1 1;
    margin-top: 5px;
    margin-right: 16px;
    margin-bottom: 16px;
}

.about-right {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 3 1;
            flex: 3 1;
    padding-left: 10px;
    margin-top: 2px;
}

.name {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.about-pic {
    margin-top: 5px;
    width: 200px;
    /*box-shadow: 0 0 5px 2px rgba(0,0,0,0.5);*/
}

.social {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 5px;
}

.social a {
    color: var(--white);
    font-size: 20px;
    padding: 5px;
    margin-right: 10px;
}

.social a:hover {
    color: var(--yellow);
    padding: 5px;
    transition: 0.2s;
}

@media (max-width: 855px) {
    .about-div p {
        font-size: 18px;
    }
    
    .about-box {
        margin: 0 20px;
    }
}

@media (max-width: 797px) {
    .about-div p {
        font-size: 17px;
        line-height: 28px;
    }

    .about-box {
        margin: 0 20px;
    }

    .resume-button {
        margin-top: 20px;
    }
}

@media (max-width: 714px) {
    .about-div p {
        font-size: 16px;
        line-height: 28px;
    }
}

@media (max-width: 700px) {
    .about-box {
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: center;
                align-items: center;
    }
    .about-right {
        min-width: 300px;
        max-width: 400px;
    }
    .about-div p {
        font-size: 16px;
        line-height: 25px;
    }
}

@media (max-width: 500px) {
    .about-box {
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: center;
                justify-content: center;
    }
    .about-right {
        min-width: 200px;
        max-width: 280px;
    }
    .about-div p {
        font-size: 14px;
        line-height: 20px;
    }
}
.nav-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    position: -webkit-sticky;
    position: sticky;
    overflow: hidden;
    top: 0;
    bottom: 0;
    z-index: 100;
}

.navbar {
    background: var(--nav);
    font-size: 18px;
    display: -webkit-flex;
    display: flex;
    /*box-shadow: 0 0 5px 2px rgba(0,0,0,0.5);*/
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    font-family: 'Inter', sans-serif;
    padding: 0 70px;
    min-height: 75px;
    max-height: 75px;
}

.gradient-div {
    background: rgb(91, 227, 207);
    background: linear-gradient(to right, var(--pink), var(--red), var(--yellow), var(--secondary), var(--primary));
    height: .3rem;
    width: 100%;
    position: -webkit-sticky;
    position: sticky;
    z-index: 100;
}

.nav-links a {
    color: var(--white);
    text-decoration: none;
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    text-transform: lowercase;
    font-weight: bold;
    padding: 30px;
}

.nav-links a:hover {
    color: var(--primary);
    cursor: pointer;
    transition: 0.2s;
}

.nav-links > .active {
    color: var(--secondary);
}

.logo {
    display: -webkit-flex;
    display: flex;
    width: 65px;
    height: 60px;
    text-align: center;
    line-height: 60px;
    font-family: 'Montserrat', sans-serif;
    font-size: 30px;
    font-weight: bolder;
    color: var(--white);
    padding: 7px;
    text-transform: uppercase;
    margin-bottom: 15px;
}

.logo:hover {
    color: var(--white) !important;
}

.pretty-colors-border {
    margin: 0;
}

.pretty-colors-border ul {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    list-style-type: disc;
    padding: 0;
    margin: 0;
}

.pretty-colors-border li {
    width: 40vw;
    height: 10px;
    display: list-item;
}

.green {
    list-style-type: none;
    background: var(--primarylight);
}

.yellow {
    list-style-type: none;

    background: var(--yellow);
}

.blue {
    list-style-type: none;

    background: var(--blue);
}
.pink {
    list-style-type: none;
    background: var(--pink);
}

@media (max-width: 500px) {
    .nav-links a {
        font-size: 15px;
        padding: 15px;
    }

    .navbar {
        -webkit-justify-content: space-evenly;
                justify-content: space-evenly;
    }

    .logo {
        font-size: 25px;
    }
}

.footer-container {
    background-color: var(--nav);
    overflow: hidden;
    position: -webkit-sticky;
    position: sticky;
    width: 100%;
    margin-top: 50px;
}

.footer-content {
    padding: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.footer-content img{
    width: 5%;
    margin-top: 10px;
    margin-bottom: 20px;
}
